@import "../../app.scss";
@import "../../../../node_modules/bulma/sass/components/navbar.sass";
@import "../../../../node_modules/bulma/sass/components/menu.sass";
.navbar {
  background-color: rgba(255, 255, 255, 0.95);
}

.navbar-end {
  margin-right: 2rem;
}

.navbar-burger {
  background-color: desaturate(lighten($secondary, 64.31372549019608), 85%);
  :hover,
  :active,
  :checked,
  :focus {
    background-color: lighten(
      desaturate(lighten($secondary, 64.31372549019608), 85%),
      5.6
    );
  }
}

.userNameDisplay {
  margin: 0rem;
  font-weight: bold;
}
.navUserName {
  margin-left: 0.25em;
}
