.navbar-item > .button {
  height: 65px;
  width: 65px;
  border-radius: 50%;
  text-align: center;
  line-height: 1em;
  background: none !important;
  border: none;
}

.navbar-item .button:focus {
  // background-color: $primary;
  color: $dark;
  border: 1px black solid;
  background-color: none;
}

.navbar-item .button:hover,
.navbar-item .button:active {
  // background-color: darken(
  //   desaturate($color: $primary, $amount: 100),
  //   $amount: 25
  // );
  // color: $light;
  border: solid 1px black;
  background-color: none;
}

nav .userphoto {
  width: 50px;
  height: 50px;
  border-radius: 10rem;
  border: solid 1px black;
  background-position: center;
  background-size: cover;
  float: left;
  margin: 0 0 0 0.5rem;
}

@media only screen and (min-width: 1024px) {
  .navbar-brand {
    margin-left: 1rem;
  }
}
@media only screen and (max-width: 1023px) {
  .navbar-end {
    margin-right: 0rem !important;
  }
  .navbar-menu {
    background-color: desaturate(lighten($secondary, 64.31372549019608), 85%);
    background-color: rgba(255, 255, 255, 0.95);
    width: 15rem;
    position: absolute;
    right: 0rem;
    text-align: right;
    padding: 1rem;
    border-radius: 0 0 0.25rem 0.25rem;
    .navbar-item {
      text-align: center;
      padding: 0 12px;
    }
    button {
      float: right;
      margin: 0.5em;
    }
    .userNameDisplay {
      margin: 0.5em auto;
      clear: right;
      float: none;
      text-align: center;
    }
    .userphoto {
      float: none;
      margin: 0 auto;
    }
  }
}
